.social-navigation ul, .legal-navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.page-template-default #wrapper ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-template-default #wrapper ul li {
  padding-left: 3.5rem;
  position: relative;
  margin-bottom: 1.5rem;
}
.page-template-default #wrapper ul li:before {
  position: absolute;
  top: 12px;
  left: 0;
  content: "";
  display: block;
  height: 2px;
  width: 2.4rem;
}

.link {
  font-weight: 500;
  position: relative;
  padding-bottom: 5px;
  width: fit-content;
}
.link span::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: black;
  width: 0;
  transition: all 0.3s ease-out;
}
.link:hover span::before {
  width: 100%;
  transition: all 0.3s ease-out;
}

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden !important;
}

body {
  position: relative;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-white);
  color: var(--color-black);
}

p,
li,
a,
small, button {
  color: #131313;
  line-height: 1.6em;
}

p,
li,
a,
figure {
  margin: 0;
  padding: 0;
}

strong {
  font-weight: 600;
}

a {
  color: var(--color-black);
  text-decoration: none;
  transition: all 0.3s;
}
a:hover {
  color: var(--color-black);
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

ul,
li {
  text-decoration: none;
  padding: 0;
  margin: 0;
  list-style: none;
}

.svg-icon-inline, .svg-icon {
  align-items: center;
  width: 1em;
  height: 1em;
  background-color: transparent;
  fill: currentColor;
}

.svg-icon {
  display: flex;
}

.svg-icon-inline {
  display: inline-flex;
}

