// --------------------------------------------------
//		Modules: Gallery
// --------------------------------------------------

@use 'settings' as *;
@use 'helper' as *;
@use 'tools' as *;


.project-gallery, .artwoks {
    @extend %module-vertical-medium; 

    height: 100%;
    background-color: color('black');

    .thumbs {
        @extend %module-top-small; 
    }

    .splide__arrow {
        background: none !important;
        height: 48px;
        width: 48px;
    }

    .splide__arrow--prev {
        left: 2vw;
    }

    .splide__arrow--next {
        right: 2vw;
    }
    
    .gallery {
        .splide__slide {
            height: 50vh;

            img {
                object-fit: contain;
                height: 100%;
            }
        }

    }

}